.container {
   max-width: 1200px;
   margin: auto;
}

.card {
   border: 1px solid #e0e0e0;
   border-radius: 8px;
   overflow: hidden;
   transition: box-shadow 0.3s;
}

.card:hover {
   box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.image-container {
   position: relative;
}

.image-container .first {
   position: absolute;
   top: 10px;
   left: 10px;
   right: 10px;
   display: flex;
   justify-content: space-between;
}

.discount {
   background-color: red;
   color: white;
   padding: 5px 10px;
   border-radius: 5px;
   font-size: 0.9em;
}

.wishlist i {
   color: #e0e0e0;
   font-size: 1.2em;
   cursor: pointer;
}

.wishlist i:hover {
   color: red;
}

.product-image {
   width: 100%;
   height: auto;
}

.product-details {
   padding: 15px;
}

.product-title {
   font-size: 1.1em;
   margin-bottom: 10px;
}

.price {
   font-size: 1.2em;
   color: #333;
}

.rating i {
   color: gold;
   margin-right: 2px;
}
